@import url(https://cd.n23.io/sites/kashimadocs/fonts/worksans.css);
@import url(https://cd.n23.io/sites/kashimadocs/fonts/worksans.css);
:root {
    --search-opacity: 1;
}

body, html {
    margin: 0;
    scrollbar-width: thin;
    scroll-behavior: smooth;
    overflow-x: hidden !important;
}

.header {
    background-color: #202225;
    height: 65px;
    width: 100%;
}

.header-content {
    background-color: #202225;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.header-application-name {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    color: white;
    font-size: 17px;
}

.header-content-logo {
    height: 55px;
    width: 55px;
}

.header-buttons img {
    filter: invert(1);
    height: 40px;
    width: 40px;
}

.header-search-bar {
    width: 50%;
    max-width: 400px;
    min-width: 130px;
}

.header-search-bar form {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-search-bar form input {
    width: 100%;
    background-color: transparent;
    border-style: none;
    border-bottom-style: solid;
    border-bottom-color: white;
    color: white;
    height: 32px;
}

.header-search-bar form button {
    background-color: transparent;
    border-style: none;
    border-bottom-style: solid;
    border-bottom-color: white;
    --search-opacity: 1;
}

.header-search-bar form button:focus, .header-search-bar form input:focus {
    outline: none;
}

.header-search-bar form button:hover {
    /*background-color: rgba(35, 35, 35, 0.38);*/
    --search-opacity: .65;
}

.header-search-bar form button:active {
    --search-opacity: .5;
}

.header-search-bar form button img {
    height: 30px;
    width: 30px;
    filter: invert(1);
    filter: invert(var(--search-opacity));
    transition: .1s all ease-in-out;
}

.main-section {
    width: 100%;
    min-height: 100vh;
    background-color: #17181A;
    display: flex;
    position: relative;
}

.faq-main-section {
    flex-direction: column;
}

.main-sidebar-child {
    display: flex;
    align-items: center;
    /*background-color: black;*/
    margin-top: 5px;
    margin-bottom: 5px;
    height: 50px;
    width: 100%;
    transition: .1s background-color ease-in-out;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: default;
}

.main-sidebar-child:hover {
    background-color: rgba(21, 21, 24, 0.76);
}

.main-sidebar-child:active {
    background-color: rgba(14, 14, 16, 0.82);
}

.main-sidebar-image {
    height: 35px;
    width: 35px;
    position: absolute;
    left: 14px;
}

.main-sidebar {
    background-color: #1F2022;
    width: 65px;
    position: relative;
    transition: .05s width ease-in-out;
    padding-top: 20px;
    z-index: 100;
}

.main-sidebar-floating {
    z-index: 100;
    position: fixed !important;
    top: 0;
}

.main-sidebar-name {
    margin: 0;
    position: absolute;
    left: 70px;
    color: white;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    transition: .1s opacity ease-in-out;
    opacity: 1;
}

.main-sidebar-name-hidden {
    opacity: 0;
}

.main-sidebar-open {
    width: 170px;
}

.main-content {
    flex: 1 1 auto;
    width: 0;
    transition: .05s margin-left ease-in-out;
}

.main-content-fixed {
    margin-left: 70px !important;
}

.main-content-fixed.main-content-open {
    margin-left: 170px !important;
}

.main-content-title {
    color: white;
    text-align: center;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 23px;
    margin-left: 15px;
}

.main-content-readme {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.452);
    min-height: 250px;
    display: flex;
    justify-content: center;
    align-items: end;
    align-items: flex-end;
    flex-direction: column;
    position: relative;
    transition: .2s all ease-in-out;
}

.main-content-readme-bg {
    background: url("https://github.com/Deivu/Kashima/blob/master/res/Kashima.jpg?raw=true") no-repeat;
    background-size: cover;
    background-position: center;
}

.main-content-readme-home {
    flex-direction: row !important;
    padding-top: 120px;
}

.readme-left {
    width: 450px;
    margin-bottom: 40px;
}

.readme-right {
    margin-left: 30px;
}

.readme-right img {
    width: 350px;
    margin-bottom: -3px;
    /* border-radius: 20px;
    border-style: solid;
    border-color: white;
    border-width: 2px; */
}

.swiper-button-prev, .swiper-button-next {
    display: none !important;
}

.readme-content-content {
    text-align: center;
}

.main-content-readme-collapsed {
    min-height: 0 !important;
    max-height: 20px !important;
    opacity: 0;
    padding-bottom: 0;
}

.readme-title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: -webkit-max-content;
    height: max-content;
    animation: fadein .6s;
}

.readme-content {
    height: -webkit-max-content;
    height: max-content;
    margin: 30px 0 0 20px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: fadein .8s;
}

.readme-content-invite-links {
    display: flex;
    margin-top: 30px;
}

.readme-content-invite-child {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #5664F1;
    border-radius: 30px;
    transition: .1s all ease-in-out;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.readme-content-invite-child:hover {
    filter: invert(.1);
}

.readme-content-invite-child:active {
    filter: invert(.2);
}

.readme-content-invite-child img {
    height: 40px;
    width: 40px;
}

.readme-content-invite-child p {
    color: white;
    font-size: 14px;
    margin: 0 10px 0 5px;
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
}

.readme-content-description {
    color: white;
    font-size: 19px;
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
}

.readme-content-content {
    color: white;
    font-size: 1.2rem;
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
}

.readme-title-title, .readme-title-desc {
    color: white;
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 34px;
    margin: 10px 0 0 0;
    text-align: center;
}

.readme-title-title {
    margin-top: 40px;
}

.readme-title-desc {
