    color: #cecece;
}

.main-content-commands {
    width: 100%;
    /*background-color: #9afffa;*/
}

.main-content-categories {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.category-parent {
    background-color: #161515;
    margin: 10px;
