.main-command {
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
    animation: fadein .5s;
    /*background-color: lightcoral;*/
}

.main-command:first-child {
    margin-top: 30px;
}

.main-command-title-div {
    display: flex;
    align-items: center;
    padding-left: 15px;
}

.main-command-commands {
    margin-left: 50px;
}

.main-command-title-div img {
