}

.main-command-title-div p {
    color: white;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 17px;
    margin: 0 0 0 5px;
    font-weight: bold;
}

.main-command-child-name, .main-command-child-desc {
    margin: 0;
    color: white;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.main-command-child-name {
    margin-top: 10px;
    font-size: 17px;
    font-weight: bolder;
}

.main-command-child-desc {
    margin: 4px auto auto 10px;
    color: #dbdbdb !important;
}

.main-command-child-args {
    margin: 6px auto 15px 25px;
}

.main-command-child-args-child {
    color: white;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 15px;
}

.main-command-child-args-a {
    color: #9afffa;
    margin: 0 2px 0 2px;
    cursor: pointer;
}

.main-command-child-description {
    margin: 3px auto 10px 15px;
