    margin: 0 2px 0 2px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    color: white;
    font-size: 15px;
}

.layer {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.49);
    z-index: 1;
    animation: fadein .3s;
}

.layer-out {
    animation: fadeout .3s;
}

.layer-main {
    width: -webkit-max-content;
    width: max-content;
    padding: 10px;
    height: -webkit-max-content;
    height: max-content;
    background-color: #262828;
    border-radius: 14px;
    box-shadow: 0 6px 5px -1px #343434;
}

.layer-main-loading {
    background-color: transparent !important;
    box-shadow: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.layer-main-loading img {
    height: 80px;
}

.layer-main-loading p {
    color: white;
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 19px;
}

.layer-main-title, .layer-main-desc, .layer-main-hint {
    color: white;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    margin-left: 10px;
}

.layer-main-title {
    font-size: 20px;
}

.layer-main-hint {
    font-size: 10px;
    text-align: center;
}

.layer-main img {
    max-width: 100%;
    border-radius: 15px;
}

.layer-main-images {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.back-to-top-btn {
    position: fixed;
    transform: translateZ(0);
    cursor: pointer;
    top: auto;
    right: 20px;
    bottom: 20px;
    left: auto;
    z-index: 1;
    width: 50px;
    height: 50px;
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: 50%;
    background-image: url("data:image/png;base64,");
}

.centered {
    position: fixed;
    top: 0;
    left: 0;
    transform: translate(calc(50vw - 50%), calc(50vh - 50%));
}

.faq-title {
