@import url('https://cd.n23.io/sites/kashimadocs/fonts/worksans.css');

:root {
    --search-opacity: 1;
}

body, html {
    margin: 0;
    scrollbar-width: thin;
    scroll-behavior: smooth;
    overflow-x: hidden !important;
}

/* .policy-title p {
    font-size: 30px !important;
} */

.policy-content {
    color: white;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    padding: 20px;
    margin-top: 50px;
}

.policy-content p {
    font-size: 1.2rem;
    text-align: left;
    margin: 5px auto 5px auto;
}

.policy-content h3 {
    margin-top: 30px;
    margin-bottom: 0;
}