.command-readme {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.68) 40%, #17181A), #656b74 no-repeat !important;
    min-height: 170px !important;
}

.command-table {
    display: flex;
    align-items: center;
    justify-content: center;
}

.commands {
    text-align: center;
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
    border-collapse: collapse;
    /*border: 3px solid #ddd;*/
    width: 98vw;
}

.commands td, .commands th {
    border: 1px solid #ddd;
    padding: 10px;
}

.commands td {
    font-family: "Work Sans", "Roboto", "Helvetica", "Arial", sans-serif;
    color: white;
    text-align: left;
}

.commands tr:hover {
    background-color: #242424;
}

.commands th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #363B37;
    color: white;
}

.cmd-table-a {
    color: rgb(145, 252, 252);
    margin-right: 7px;
    cursor: pointer;
    transition: .2s all ease-in-out;
}

.cmd-table-a:hover {
    color: rgb(124, 209, 209);
}